* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
}

html {
  color-scheme: light dark;
  font-family: Arial, Helvetica, sans-serif;
}

#debug {
  z-index: 9999;
  color: #fff;
  background-color: #000000b3;
  max-width: 300px;
  max-height: 150px;
  padding: 4px 8px;
  font-size: 12px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.hide {
  display: none;
}

.app {
  cursor: default;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.screen-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.screen {
  user-select: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.title-screen {
  z-index: 1;
  color: #333;
  text-align: center;
  background-color: #fff;
  font-size: 16px;

  & .title-content {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: 300px;
    display: flex;
  }

  & .title-buttons {
    flex-direction: column;
    justify-content: left;
    gap: 24px;
    min-width: 250px;
    display: flex;
  }

  & .labeled-button {
    flex-direction: row;
    justify-content: right;
    align-items: center;
    gap: 12px;
    display: flex;

    & label {
      color: #222;
      font-size: 1.2rem;
      line-height: 1.2rem;
      display: block;
    }
  }

  & .round-button {
    color: #222;
    cursor: pointer;
    will-change: transform;
    background-color: #fff;
    border: 1px solid #222;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    transition: transform .12s, box-shadow .12s;
    display: block;
    box-shadow: 0 2px 6px #0000004d;

    &:not(:disabled):active {
      transform: scale(.9);
      box-shadow: 0 1px 3px #00000059;
    }
  }

  & .progress-bar {
    border: 1px solid #222;
    border-radius: 8px;
    max-width: 250px;
    height: 15px;

    &::part(bar) {
      background-color: #87e3ff;
      border-radius: 8px;
      transition: width 1.2s;
    }
  }
}

.calendar-screen {
  z-index: 0;
}

.pan-container {
  flex: 1 1 0;
  width: 100%;
  height: 100%;
}

blur-overlay {
  --width: 15px;
  --edge-opacity: .4;
}

audio-toggle {
  position: fixed;
  bottom: 20px;
  right: 80px;

  &:active {
    transform: scale(.9);
    box-shadow: 0 1px 3px #00000059;
  }
}

fullscreen-toggle {
  position: fixed;
  bottom: 20px;
  right: 20px;

  &:active {
    transform: scale(.9);
    box-shadow: 0 1px 3px #00000059;
  }
}
